import React, {useEffect, useState} from "react";
import style from "./items.module.css"
import {FaSteam} from "react-icons/fa";
import {NavLink} from "react-router-dom";
import ReactPaginate from "react-paginate";
import {IoCart} from "react-icons/io5";
import {GrFormPrevious, GrFormNext} from "react-icons/gr";

const Items = (props) => {

    console.log(props.sellLeaders)

    function Items({ currentItems }) {
        return (
            <ul  className={style.wrapItems}>
                {currentItems && currentItems.map(item =>
                    <li key={item.id} className={style.itemLi}>
                        <NavLink className={style.navLinkStyle} to={`/product/${item.id}`}>
                            <img className={style.imgItemStyle} src={item.imgItem}
                                />
                            <div className={style.panelItem}>
                                <span className={style.countSellImg}><IoCart/></span>
                                   <span className={style.countSell}>{item.cnt_sell}</span>
                                <span className={style.priceItem}>{item.price} ₽</span>
                            </div>
                        </NavLink>
                    </li>
                )}
            </ul>
        );
    }

    function PaginatedItems({ itemsPerPage}) {

        const [currentItems, setCurrentItems] = useState(props.sellLeaders);
        const [pageCount, setPageCount] = useState(0);
        const [itemOffset, setItemOffset] = useState(0);

        useEffect(() => {
            const endOffset = itemOffset + itemsPerPage;
            setCurrentItems(props.sellLeaders.slice(itemOffset, endOffset));
            setPageCount(Math.ceil(props.sellLeaders.length / itemsPerPage));
        }, [itemOffset, itemsPerPage]);


        const handlePageClick = (event) => {
            const newOffset = (event.selected * itemsPerPage) % props.sellLeaders.length;
            setItemOffset(newOffset);
        };

        return (
            <>
                <Items currentItems={currentItems} />
                <div>
                    <ReactPaginate
                        breakLabel="..."
                        nextLabel=<GrFormNext />
                        onPageChange={handlePageClick}
                        pageRangeDisplayed={2}
                        pageCount={pageCount}
                        previousLabel=<GrFormPrevious />
                        pageLinkClassName={style.itemLink}
                        previousLinkClassName={style.itemLink}
                        nextLinkClassName={style.itemLink}
                        containerClassName={style.paginator}
                        activeLinkClassName={style.activeItem}
                        renderOnZeroPageCount={null}
                    />
                </div>
            </>
        );
    }


    return (
       <PaginatedItems itemsPerPage={15} />
    )
}

export default Items;