import React, {useEffect, useState} from "react";
import Slider from "../slider/sliderContainer";
import style from "./main.module.css";
import Items from "./itemsMainBlock/itemsContainer"
import {FaCrown} from "@react-icons/all-files/fa/FaCrown";
import {MdOutlineRecommend, TbDiscount2} from "react-icons/all";
import {requestItemsFromCategories} from "../../redux/productsReducer";
const Main = (props) => {

    let [initSlider, setInitSlider] = useState(props.initSlider)
    let [selectCategory, setSelectCategory] = useState("lead");

    useEffect(() => {
        setInitSlider(props.initSlider)
        console.log(initSlider)
    }, [initSlider, props.initSlider])

    let activeLeaders = (e) => {
        if (selectCategory != e.target.id) {
            setActiveRec(false);
            setActiveDiscount(false);
            setActiveLead(true);
            setSelectCategory(e.target.id);
            props.requestItemsFromCategories("lead");
        }
    }
    const [activeLead, setActiveLead] = useState(true);

    let activeRecommend = (e) => {
        if (selectCategory != e.target.id) {
            setActiveRec(true);
            setActiveDiscount(false);
            setActiveLead(false);
            setSelectCategory(e.target.id);
            props.requestItemsFromCategories("rec");
        }
    }
    const [activeRec, setActiveRec] = useState(false);

    let activeDiscount = (e) => {
        if (selectCategory != e.target.id) {
            setActiveRec(false);
            setActiveDiscount(true);
            setActiveLead(false);
            setSelectCategory(e.target.id);
            props.requestItemsFromCategories("dis");
        }
    }
    const [activeDis, setActiveDiscount] = useState(false);

    return (
        <div className={style.mainWrap}>
        <div style={{
            padding: 0
        }} className="container-lg center">
            {initSlider && <Slider />}
            <br />
            <div className="container">
                <div className="row">
                    <div className="col-lg">
                        <span onClick={activeLeaders} id="lead" className={activeLead ? style.menuItemActive : style.menuItem}>Лидеры продаж <FaCrown /></span>
                    </div>
                    <div className="col-lg">
                        <span onClick={activeRecommend} id="rec" className={activeRec ? style.menuItemActive : style.menuItem}>Рекомендуем <MdOutlineRecommend /></span>
                    </div>
                    <div className="col-lg">
                        <span onClick={activeDiscount} id="dis" className={activeDis ? style.menuItemActive : style.menuItem}>Скидки <TbDiscount2 /></span>
                    </div>
                </div>
            </div>

            <Items />

            </div>
        </div>
    )
}

export default Main;