import React from "react";
import {compose} from "redux";
import {connect} from "react-redux";
import {withRouter} from "react-router-dom";
import Main from "./main";
import {requestItemsFromCategories} from "../../redux/productsReducer";

let mapStateToProps = (state) => {
    return {
        slidesResult: state.SlidesPage.slidesResult,
        initSlider: state.SlidesPage.initSlider
    }
}

export default compose(connect(mapStateToProps, {requestItemsFromCategories}), withRouter) (Main);