import React from "react";
import {compose} from "redux";
import {connect} from "react-redux";
import {withRouter} from "react-router-dom";
import Items from "./items";
import {requestItemsFromCategories} from "../../../redux/productsReducer";

let mapStateToProps = (state) => {
    return {
        listCategories: state.SlidesPage.listCategories,
        sellLeaders: state.SlidesPage.sellLeaders
    }
}

export default compose(connect(mapStateToProps, {requestItemsFromCategories}), withRouter) (Items);